#topContainer {
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  padding-top: 60px;
  width: 100vw;
  max-width: 1100px;
  min-width: 350px;
}

.contentHero {
  display: flex;
  /* flex-direction: column; */
  align-items: flex-start;
  width: 100%;
  min-width: 350px;
}

.contentDiv {
  background-color: #2C3238;
  /* border-radius: 0px 0px 6px 6px; */
  min-width: 350px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 0px 12px 12px 12px;
  /* flex-shrink: 1; */
  margin-top: 150px;
}

.contentDiv2 {
  background-color: #2C3238;
  border-radius: 0px 0px 6px 6px;
  min-width: 350px;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0px 12px 12px 12px;
  margin-top: 20px;
  /* flex-shrink: 1.6; */
}

#titleName {
  font-family: 'Montserrat', sans-serif;
  font-weight: 700;
  color: #E4AF23;
  font-size: 5em;
}

#subName {
  color: #F37433;
  font-size: 2.8em;
  font-family: 'Montserrat', sans-serif;
  font-weight: 200;
  margin-top: 5px;
}

#subName2 {
  color: whitesmoke;
  font-size: 2.2em;
  font-family: 'Montserrat', sans-serif;
  font-weight: 200;
  margin-top: 5px;
}

#meHeadshot {
  /* padding-left: 14px; */
  margin-top: 100px;
}

@media screen and (orientation: portrait) {
  #topContainer {
    flex-direction: column;
  }
  .contentDiv2 {
    margin-top: 10px;
  }
}
