#modal {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

#modal-background {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.7);
}

#modal-content {
  position: absolute;
  background-color: rgb(112, 122, 139);
  display: flex;
  flex-direction: column;
  align-items: center;
  border: 2px solid rgb(30, 32, 34);
  border-radius: 20px;
}
